<template>
  <div>
    <b-card>
      <b-row>
        <b-col
          class="buscar"
          cols="10"
          md="3"
        >
          <b-form-group>
            <b-input-group>
              <b-input-group-prepend is-text>
                <feather-icon
                  icon="SearchIcon"
                  size="1.4x"
                  class="text-info"
                />
              </b-input-group-prepend>
              <b-form-input v-model="search" />
            </b-input-group>
          </b-form-group>
        </b-col>
        <b-col
          cols="12"
          md="7"
          class="text-center nombreTabla"
        >
          <h1 class="font-weight-bold text-info">
            Historial de Carga de leche
            <feather-icon
              icon="TruckIcon"
              size="1.1x"
            />
          </h1>
        </b-col>
        <b-col
          class="accion"
          cols="2"
          md="2"
        />
      </b-row>
      <b-row>
        <b-col
          cols="12"
          md="12"
        >
          <b-table
            responsive="sm"
            :items="items"
            :fields="fields"
            :filter="search"
            :filter-function="filterTable"
            :per-page="perPage"
            :current-page="currentPage"
            :table-class="'text-capitalize text-center'"
            hover
            striped
          >
            <template #cell(id)="data">
              <div class="font-weight-bold text-info">
                #{{ data.value }}
              </div>
            </template>
            <template #cell(liters)="data">
              <div class="font-weight-bold">
                {{ data.value }} Lts
              </div>
            </template>
            <template #cell(created_at)="data">
              <div class="font-weight-bold text-primary">
                {{ new Date(data.value).toLocaleDateString('es-ES') }}
              </div>
            </template>
          </b-table>
        </b-col>
      </b-row>
      <b-row>
        <b-col
          cols="12"
          md="12"
        >
          <b-pagination
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            align="center"
          />
        </b-col>
      </b-row>
    </b-card>
  </div>
</template>

<script>
import {
  BCard,
  BTable,
  BFormGroup,
  BFormInput,
  BCol,
  BRow,
  BPagination,
  BInputGroup,
  BInputGroupPrepend,
} from 'bootstrap-vue'
import { mapState } from 'vuex'

export default {
  components: {

    BCard,
    BTable,
    BFormGroup,
    BFormInput,
    BCol,
    BRow,
    BPagination,
    BInputGroup,
    BInputGroupPrepend,
  },
  data() {
    return {
      open: false,
      item: null,
      search: '',
      perPage: 10,
      currentPage: 1,
      fields: [
        {
          key: 'id',
          label: 'N°',
          sortable: true,
        },
        {
          key: 'producer.name',
          label: 'Productor',
          sortable: true,
        },
        {
          key: 'carrier.name',
          label: 'Rutero',
          sortable: true,
        },
        {
          key: 'liters',
          label: 'Litros',
        },
        {
          key: 'created_at',
          label: 'Fecha',
          variant: 'success',
        },
      ],
    }
  },
  computed: {
    ...mapState({
      items: state => state.milkLoad.items,
    }),
    rows() {
      return this.items.length
    },
  },
  mounted() {
    this.getItems()
  },
  methods: {
    /// ////////////  Funcion Mostrar //////////////////
    getItems() {
      this.$store.dispatch('milkLoad/get')
    },
    filterTable(item, filter) {
      const searchTerm = filter.toLowerCase()
      return (
        item.id.toString().includes(searchTerm)
        || item.producer.name.toLowerCase().includes(searchTerm)
        || item.carrier.name.toLowerCase().includes(searchTerm)
      )
    },
  },
}
</script>

   <style>
   @media (max-width: 768px) {

     .buscar:nth-child(1) {
       order: 2;
     }
     .nombreTabla:nth-child(2) {
       order: 1;
     }
     .accion:nth-child(3) {
       order: 3;
     }
     .b-sidebar {
       width: 80% !important;
     }
   }
   </style>
